'use client';

import { Play, Plus, Tv } from 'lucide-react';
import styles from './Hero.module.css';

export default function Hero() {
    return (
        <section className={styles.hero}>
            {/* Background with Vignette */}
            <div className={styles.heroBackground}>
                <div className={styles.heroImage} />
                <div className={styles.gradientOverlay} />
                <div className={styles.leftVignette} />
                <div className={styles.bottomVignette} />
            </div>

            <div className={styles.content}>
                <div className={styles.originalLabel}>Kyno+ Original</div>

                <h1 className={styles.title}>Eternidade</h1>

                <div className={styles.meta}>
                    <span className={styles.newBadge}>New</span>
                    <span>2025</span>
                    <span className={styles.rating}>★ 7.1</span>
                </div>

                <div className={styles.buttons}>
                    <button className={styles.watchBtn}>
                        <Play className="fill-black" size={20} />
                        Watch Now
                    </button>

                    <button className={styles.actionBtn}>
                        <Plus size={24} />
                        <span>My List</span>
                    </button>

                    <button className={styles.actionBtn}>
                        <Tv size={24} />
                        <span>Trailer</span>
                    </button>
                </div>

                <p className={styles.description}>
                    Numa vida após a morte, onde as almas têm uma semana para decidir onde passar a eternidade, Joan enfrenta a escolha mais difícil de sua existência...
                </p>

                <div className={styles.genres}>
                    Romance • Comédia
                </div>
            </div>
        </section>
    );
}
